.formdiv {
  width: 70%;
  margin: auto;
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 550px;
  font-size: 15px;
  font-family: Arial, Helvetica, sans-serif;
}
.formtitle {
  color: #ffff;
  font-size: 30px;
  font-weight: bold;
  margin-top: 0px;
}
.addForm {
  background-color: rgba(0, 0, 0, 0.363);
  padding: 50px;
  height: 80%;
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  animation: fadein 1s;
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.inputs {
  font-family: Arial, Helvetica, sans-serif;
  height: 40px;
  width: 300px;
  margin-top: 10px;
  font-size: 15px;
  padding-left: 10px;
  border: unset;
}
textarea {
  font-family: Arial, Helvetica, sans-serif;
  height: 100px;
  width: 300px;
  margin-top: 10px;
  font-size: 15px;
  padding-left: 10px;
  padding-top: 10px;
  border: unset;
}
textarea:focus,
input:focus {
  outline: none;
}
.btncontainer {
  display: flex;
  justify-content: flex-end;
}
.btn {
  width: 105px;
  height: 40px;
  font-size: 15px;
  font-weight: bold;
  border: unset;
  border: 4px solid rgba(134, 134, 134, 0.473);
  margin-top: 10px;
  margin-bottom: 10px;
  color: rgba(0, 0, 0, 0.719);
  letter-spacing: 1px;
}
.btn:hover {
  border: 3px solid rgba(250, 189, 109, 0.945);
}

@media (max-width: 800px) {
  .addForm {
    height: 450px;
    width: 300px;
  }
  .formtitle {
    font-size: 20px;
  }
}
